import React from 'react'
import Container from '../components/Container'
import '../css/about.css'
import text from '../translations/about.json'

export default function About({lang}) {
    return (
        <Container lang={lang} dark={false}>
            <div className="aboutpage">
                <header className="header">
                    {text[lang][0]}
                </header>
                <section>
                    {text[lang][1]}<br/>{text[lang][2]}
                    <div className="bigtext">Pain is temporary, Glory is forever</div>
                </section>
            </div>
        </Container>
    )
}
